import { Modal } from '../modal/modal';

import { useModalControls } from '../../hooks/useModalControls';
import { useDispatch, useSelector } from 'react-redux';
import { ERROR_STATE } from '../../services/actions/errorHandler'

export const ErrorHandler = () => {
  // @ts-ignore
  const ErrorHandler = useSelector(store => store.errorHandler);
  const dispatch = useDispatch();
  
  const modalControls = useModalControls({
    isOpen:true,
    closeCallback: () => dispatch({type:ERROR_STATE, payload: false})
  });
  return (
    <>
        {( ErrorHandler.status && ErrorHandler.message ) ? (
          <Modal {...modalControls}>
            <div className={'mb-5'}>
                <p className='text text_type_main-large pt-4 mb-5'>!</p>
                <p className='text text_type_main-medium mb-5'>! -   . <br></br>    .</p>
                <p className='text text_type_main-default text_color_inactive'> : {ErrorHandler.message}</p>
            </div>
          </Modal>
        ) : false}
    </>
  )
}
